import React from "react";
import { useEffect, useState } from 'react'
import Home from './Home.js'
import {ArtWorksProvider} from './Contex.js'
import Card from './Card.js'
import {BrowserRouter as Router} from "react-router-dom"
import NavBar from './Link'
import Routing from "./Rounting.js";
import Whether from "./Whether.js";

function App() {
  const [worksArt,setWorksArt]=useState();
  useEffect(()=>{
    return (fetch("https://api.artic.edu/api/v1/artworks")
    .then(res=>res.json())
    .then(worksArtData=>setWorksArt(worksArtData.data))
    )},[])   

   
  return (
    <ArtWorksProvider value={worksArt}>

<Router>
  <NavBar/>
    <Routing/>
    <Whether></Whether>
    </Router>
    </ArtWorksProvider>

  );
}




export default App;
