import { useContext } from "react";
import { ArtWorksContext } from "./Contex";
export default function Card() {
  const artWorksArray = useContext(ArtWorksContext);

  return (
    <div>
      {artWorksArray?.map((artWork) => (
        <h1>{artWork.title}</h1>
      ))}
    </div>
  );
}
